import React from "react";
import Layout from "../components/layout";
import Seo from "../components/seo";
import BreadCrumb from "../components/common/breadcrumb";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import { graphql } from "gatsby";
import P2PExchangeScreen from "../screens/p2p-exchange-development";

const P2PExchangeDevelopment = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "P2P Crypto Exchange Development Company", link: null },
  ];
  return (
    <Layout db={db}>
      <BreadCrumb items={breadcrumbLinks} />
      <main>
        <P2PExchangeScreen db={db} />
      </main>
    </Layout>
  );
};

export const Head = (props) => {
  const { data } = props;
  const fileUrl =
    data?.p2pCryptoImg?.childImageSharp?.gatsbyImageData?.images?.fallback
      ?.src;
  return (
    <Seo
      title="P2P Crypto Exchange Development Company | Justtry Technologies"
      metaTitle="P2P Crypto Exchange Development Company | Justtry Technologies"
      description="Elevate your crypto game with Justtry’s bespoke P2P crypto exchange development services. Build the next-gen P2P cryptocurrency exchange today."
      image={fileUrl}
    ></Seo>
  );
};

export default P2PExchangeDevelopment;

export const queryData = graphql`
  query myQuery {
    p2pCryptoImg: file(relativePath: { eq: "p2p-crypto.png" }) {
      childImageSharp {
        gatsbyImageData
      }
    }
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
  }
`;
